<template>
  <div class="home">
    <van-search
      v-model="value"
      show-action
      shape="round"
      placeholder="请输入搜索关键词"
      @focus="onSearch"
    >
    </van-search>
    <div class="Carousel">
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image.pic" alt="" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="song-list">
      <div class="song-title">精品歌单</div>
      <div class="details">
        <div
          class="remd_li"
          v-for="(value, index) in songlist.slice(0, 10)"
          :key="index"
          @click="remddetail(value.name, value.id)"
        >
          <div class="remd_img">
            <img :src="value.coverImgUrl" alt="" />
          </div>
          <div class="remd_text">{{ value.name }}</div>
        </div>
      </div>
    </div>
    <div class="newsong">
      <div class="song-title">推荐新音乐</div>
      <van-cell is-link v-for="(value, index) in Newsong" :key="index">
        <template #title>
          <span class="custom-title">{{ value.name }}</span>
          <div class="Introduction">
            <span v-for="(val, index) in value.song.artists" :key="index">{{
              val.name + '-'
            }}</span>
            <span>{{ value.song.album.name }}</span>
          </div>
        </template>
        <template #right-icon>
          <van-icon name="play-circle" class="play-circle" size="14" />
        </template>
      </van-cell>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data () {
    return {
      value: '',
      images: [],
      active: 0,
      songlist: [],
      Newsong: []
    }
  },
  created: function () {
    this.highquality()
    this.newsong()
    this.banner()
  },
  mounted: function () {

  },
  methods: {
    onSearch () {
      this.$router.push('/search')
    },
    highquality () {
      this.api.highquality().then(res => {
        this.songlist = res.data.playlists
      }).catch(err => {
        Toast(err)
      })
    },
    newsong () {
      this.api.newsong().then(res => {
        this.Newsong = res.data.result
      }).catch(err => {
        console.log(err)
      })
    },
    banner () {
      let data = {
        type: '2'
      }
      this.api.banner(data).then(res => {
        if (res.data.code == 200) {
          this.images = res.data.banners
        }
      }).catch(err => {
        console.log(err)
      })
    },
    remddetail (name, id) {
      this.$router.push({
        path: '/playlistdetail',
        query: {
          id: id,
          name: name,
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.home {
  margin: 0 24px 160px 24px;

  /deep/ .van-cell {
    line-height: 50px;
  }

  /deep/ .van-field__control {
    font-size: 24px;
  }

  /deep/ .van-search__action {
    font-size: 24px;
  }

  /deep/ .van-icon {
    font-size: 28px;
  }

  .Carousel {
    height: 300px;

    /deep/ .van-swipe {
      height: 300px;
      border-radius: 24px;
    }
    /deep/ .van-swipe__indicator--active {
      width: 16px !important;
      height: 6px !important;
      background-color: rgb(185, 39, 39) !important;
      border-radius: 4px;
    }
    /deep/ .van-swipe__indicator {
      width: 16px !important;
      height: 6px !important;
      background-color: rgb(248, 238, 238);
      border-radius: 4px;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }
  .song-list {
    width: 100%;
    margin-top: 32px;

    .song-title {
      font-size: 32px;
      text-align: left;
    }
    .details {
      width: 100%;
      padding: 20px 0;
      display: flex;
      overflow-x: auto;

      .remd_li {
        height: 270px;

        .remd_img {
          width: 220px;
          height: 220px;
          border-radius: 16px;
          overflow: hidden;
          margin-right: 20px;

          img {
            width: 100%;
            height: 100%;
          }
        }
        .remd_text {
          width: 220px;
          font-size: 24px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin-top: 10px;
        }
      }
    }
  }
  .newsong {
    .song-title {
      font-size: 32px;
      text-align: left;
      margin: 20px 0;
    }

    .custom-title {
      font-size: 28px;
    }

    /deep/ .van-cell {
      line-height: normal;
      padding: 8px 0;
    }

    /deep/ .van-cell__title {
      width: 60%;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-wrap: wrap;
      flex: 1;
    }

    /deep/ .van-cell__title span {
      width: 100%;
      text-align: left;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    /deep/ .van-cell__label {
      text-align: left;
    }

    /deep/ .van-icon {
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: flex-end;
    }

    .Introduction {
      color: rgba(0, 0, 0, 0.6);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 24px;
      text-align: left;
    }
  }
}
</style>